.Facts{
    padding: 10rem 4rem;

    h2{
        font-size: 4rem;
        line-height: 6rem;
        margin-bottom: 2rem;
        color: #133639;

    }

    hr{
        width: 20%;
        height: 0;
        border: 1px solid #000000;
        border-bottom: 0;
        border-left: 0;
        border-right: 0;
        margin: 0 auto 8rem auto;
    }

    section{
        display: flex;
        align-items: center;
        justify-content: space-around;
        gap: 2rem;
    }

    .FiguresCard{
        padding: 5rem 3rem 5rem 3rem;
        border-radius: 10px;
        width: 18rem;
        color: #FFFFFF;

        .Figure{
            font-size: 4rem;
            margin-bottom: 1.5rem;
        }

        .Desc{
            font-size: 2.2rem;
            
        }
    }
}


@media screen and (max-width: 1000px) {
    .Facts{
        padding: 5rem 1rem;

        h2{
            font-size: 2.5rem;
            margin-bottom: 1rem;
        }

        hr{
            width: 40%;
            margin: 0 auto 8rem auto;
        }

        section{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            gap: 2rem;
        }
    }

}

@media screen and (width: 280px){
    .Facts{
        .FiguresCard{
            width: 15rem;
            padding: 2rem;
        }
    }
}