    padding: 0.2rem 2.5rem;
    border-radius: 5px;
    color: #FFFFFF;
    font-size: 2rem;
    font-family: "Wisteria Serif";
    position: relative;
    cursor: pointer;

    svg{
        margin-left: 0.5rem;
        position: absolute;
    }
